export default {
  data() {
    return {
      fetchMethodKey: 'search',
      inquire: {
        pageNum: 1,
        pageSize: 10
      },
      pageSizes: Object.seal([10, 20, 30, 50]),
      pageLayout: 'total, prev, pager, next, sizes, jumper'
    }
  },
  methods: {
    indexMethod(index) {
      return (this.inquire.pageNum - 1) * this.inquire.pageSize + (index + 1)
    },
    pageSizeChange(val) {
      this.inquire.pageSize = val
      this.inquire.pageNum = 1
      this[this.fetchMethodKey]()
    },
    pageCurrentChange(val) {
      this.inquire.pageNum = val
      this[this.fetchMethodKey]()
    }
  }
}



