import request from '../index.js'

export function getWork(data) {
  return request({
    url: '/sysApi/get_works',
    method: 'post',
    data
  })
}

export function deleteWork(data) {
  return request({
    url: '/sysApi/deleteWork',
    method: 'post',
    data
  })
}



