<template>
  <div class="app-main">
    <div class="margin-t">
      <el-button type="primary" :loading="loadinghandleSearch" @click="handleSearch"> 查询 </el-button>
      <el-button @click="reset"> 重置 </el-button>
      <el-button type="primary" :disabled="multipleSelection.length !== 1" @click="operatTable('edit')"> 修改 </el-button>
      <el-button type="danger" :disabled="multipleSelection.length === 0" @click="deleteData"> 删除 </el-button>
    </div>
    <div class="search-body margin-t-sm">
      <div class="fl margin-t-sm margin-r-sm">
        <span>用户名：</span>
        <el-input v-model="username" clearable class="input-w" placeholder="请输入" />
      </div>
    </div>
    <div class="margin-t-lg">
      <el-table
        ref="Table"
        :data="tableData"
        stripe
        border
        height="550"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="55"
        />
        <!-- <el-table-column type="index" width="65" align="center" label="序号" :index="indexMethod" /> -->
        <el-table-column
          prop="id"
          label="id"
          align="center"
          min-width="40"
        />
        <el-table-column
          prop="username"
          label="用户id"
          align="center"
          min-width="80"
        />
        <el-table-column
          prop="input_text"
          align="center"
          label="文案"
          min-width="200"
        />
        <el-table-column
          prop="output_img_path"
          align="center"
          label="预览封面路径"
          min-width="200"
        />
        <el-table-column
          prop="input_file_path"
          align="center"
          label="用户文件"
          min-width="200"
        />
        <el-table-column
          prop="credit_cost"
          align="center"
          label="花费积分"
          min-width="80"
        />
        <el-table-column
          prop="input_audio_path"
          align="center"
          label="用户语音文件"
          min-width="200"
        />
        <el-table-column
          prop="output_video_path"
          align="center"
          label="数字人路径"
          min-width="200"
        />
        <el-table-column
          prop="username"
          align="center"
          label="创建人"
          width="120"
        />
        <el-table-column
          prop="create_time"
          align="center"
          label="创建时间"
          width="160"
        />
        <!-- <el-table-column
          prop="modify_user"
          align="center"
          label="修改人"
          width="120"
        />
        <el-table-column
          prop="modify_time"
          align="center"
          label="修改时间"
          width="120"
        /> -->
      </el-table>
      <div class="text-right margin-t-sm">
        <el-pagination
          background
          :page-sizes="pageSizes"
          :current-page="inquire.pageNum"
          :page-size="inquire.pageSize"
          :layout="pageLayout"
          :total="tabletotal"
          @size-change="pageSizeChange"
          @current-change="pageCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { getWork, deleteWork } from '@/api/baseinfo/userWorks.js'
import page from '@/mixins/page'

export default {
  name: 'UserWorks',
  mixins: [page],
  data() {
    return {
      imgUrl: process.env.VUE_APP_BASE_API + '/index/ucenter/upload_img',
      site_code: '',
      username: '',
      multipleSelection: [],
      time_type: '1',
      is_enable: 1,
      start_time: '',
      end_time: '',
      tabletotal: 0,
      options: [{
        value: 1,
        label: '是'
      }, {
        value: 0,
        label: '否'
      }],
      tableData: [],
      dialogFormAdd: false,
      dialogVisibleIfarme: false,
      form: {
        site_code: '',
        site_name: '',
        item: [],
        start_time: '',
        end_time: '',
        is_enable: '1',
        remark: ''
      },
      formType: '',
      rules: {
        site_name: [
          { required: true, message: '请输入网点名称', trigger: 'blur' }
        ],
        site_code: [
          { required: true, message: '请输入网点编码', trigger: 'blur' }
        ],
        is_enable: [
          { required: true, message: '请选择启用状态', trigger: 'change' }
        ],
        item: [
          { required: true, message: '服务项目必选', trigger: 'change' }
        ],
        start_time: [
          { required: true, message: '请选择开始履行合同时间', trigger: 'change' }
        ],
        end_time: [
          { required: true, message: '请选择结束履行合同时间', trigger: 'change' }
        ],
        remark: [
          { required: false, message: '', trigger: 'blur' }
        ]
      },
      rolelist: [],
      formLabelWidth: '140px',
      exportUrl: '',
      dialogTitle: '',
      fileList: [],
      centerDialogVisible: false,
      imgShowUrl: [],
      secoptions: [],
      secLoading: false,
      loadinghandleSearch: false,
      loadingsubmit: false
    }
  },
  created() {
    this.search()
  },
  methods: {
    handleSearch() {
      this.inquire.pageNum = 1
      this.search()
    },
    async search() {
      this.loadinghandleSearch = true
      const data = new FormData()
      data.append('username', this.username)
      data.append('pageSize', this.inquire.pageSize)
      data.append('page', this.inquire.pageNum)
      const res = await getWork(data)
      this.loadinghandleSearch = false
      this.tableData = res.data.rows || []
      this.tabletotal = res.data.total
    },
    reset() {
      this.site_code = ''
      this.time_type = '1'
      this.start_time = ''
      this.end_time = ''
      this.is_enable = 1
    },

    close() {
      this.loadingsubmit = false
      this.dialogFormAdd = false
      this.$refs.form.resetFields()
    },
    operatTable(type) {
      this.dialogTitle = type === 'add' ? '新增网点' : '修改网点信息'
      this.formType = type
      this.form = {
        site_code: '',
        site_name: '',
        item: [],
        start_time: '',
        end_time: '',
        is_enable: 1,
        remark: ''
      }
      this.fileList = []
      this.dialogFormAdd = true
      if (type === 'edit') {
        if (this.multipleSelection.length !== 1) {
          this.$message.error('请选择一条数据进行修改!')
          return
        } else {
          const row = this.multipleSelection[0]

          this.form = {
            site_code: row.site_code,
            site_name: row.site_name,
            item: row.item_arr,
            start_time: row.start_time,
            end_time: row.end_time,
            is_enable: row.is_enable,
            remark: row.remark
          }

          const fileList = []
          for (let i = 0; i < row.img_url_arr.length; i++) {
            const arrItem = {}
            arrItem.name = row.img_url_arr[i].short_url
            arrItem.url = row.img_url_arr[i].long_url
            fileList.push(arrItem)
          }
          this.fileList = fileList
        }
      } else {
        this.$nextTick(() => {
          this.$refs['form'].clearValidate()
        })
      }
    },
    deleteData() {
      if (this.multipleSelection.length === 0) {
        this.$message.error('请选择要删除的数据!')
        return
      }
      this.$confirm('此操作将永久删除选中的' + this.multipleSelection.length + '条数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const arr = this.multipleSelection.map((item) => {
          return item.id
        })
        deleteWork({ id: arr }).then(res => {
          if (res.success) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.search()
          } else {
            this.$message.error(res.msg)
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    }
  }
}
</script>

<style lang="scss" scoped>
.search-body{
  display: flex;
  flex-wrap: wrap;
  >*{
    display: inline-block;
  }
  .search-item:nth-child(1){
    width:320px ;
    >*{
      display: inline-block;
      padding-bottom: 8px;
    }
    span{
      width: 100px;
      font-size: 14px;
    }
  }
  .search-item:nth-child(2){
    width:350px ;
    >*{
      display: inline-block;
    }
    span{
      width: 130px;
      font-size: 14px;
    }
  }
  .search-item:nth-child(3){
    width:160px ;
    >*{
      display: inline-block;
    }
    span{
      width: 76px;
      font-size: 14px;
    }
  }
}
.show_img{
  width: 100%;
}
.dialog-footer-c{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.app-main{
  width:80%;
  margin: 30px auto;
}
</style>



